@import "navbar";

h1 {
    margin: auto;
}
.bigHead {
    color: $accentColour;
    padding-left: 5px;
    background-color: rgb(230, 230, 230);
}

h2{
    margin: auto;
}

body {
    background-color: rgb(230, 230, 230);
    color: white;
}

.layoutContainer {
    margin: 0px;

    padding-left: 10%;
    padding-right: 10%;

}
.contentArea {
    margin-bottom: 0px;
    padding-top: 10px;
    background-color: #999;
    padding-bottom: 10px;
    box-shadow: 0px -5px inset $accentColour;

    h2 {
        padding-left: 10px;
        border-bottom: 1px solid $accentColour;
        
    }
    p {
        padding-left: 15px;
        padding-right: 15px;
    }
    a {
        font-weight: bold;
        text-decoration: none;
        color: $accentColour;
        @include fader(all, 1s, ease-in-out);


        &:hover {
            color: white;
            @include fader(all, 1s, ease-in-out);

            
        }
    }
}
.bandcampBox {
max-width: 80%;
margin-left: 2%;

}
.writingNav {
    background-color: rgb(255, 255, 255);
    padding-bottom: 0px;
    margin: auto;
    box-shadow: 0px -5px inset $accentColour;
    ul {
        margin: 0;
        padding-top: 10px;
        padding-bottom: 10px;
        list-style: none;
    }
    li {
        padding-left: 10px;
        display: inline-block;
    }
    a {
        border-right: 1px solid $accentColour;
        padding-right: 10px;
        text-decoration: none;
        color: #999;
        @include fader(all, 1s, ease-in-out);

        &:hover{
            color: $accentColour;
            @include fader(all, 1s, ease-in-out);
        }
    }
}
.lastListItem  {
    a {
        border-right: none;
    }
}
.pageIndicator {
    text-align: center;
    padding: 0;
    margin: 0;
}
.footerButtons {
    margin:0px;
    background-color: #999;
    padding-bottom: 10px;
    box-shadow: 0px -5px inset $accentColour;
    button {
        margin-top: 4px;
        color: white;
        border: 1px solid $accentColour;
        background-color: #999;
        margin-left: 5px;
    }
}
.nextButton {
    float: right;
    margin-left: 0px;
    margin-right: 5px;
    
}

@media only screen and (max-width: 960px) {
    .writingNav {
        background-color: rgb(255, 255, 255);
        padding-bottom: 0px;
        margin: auto;
        box-shadow: 0px -5px inset $accentColour;
        ul {
            margin: 0;
            padding-left: 0;
            padding-top: 10px;
            padding-bottom: 5px;
            list-style: none;
        }
        li {
            padding-top:10px;
            padding-left: 0;
            padding-bottom: 8px;
            display: block;
            text-align: center;
        }
        a {
            padding-left: 0;
            padding-right: 0;
            border-right: none;
            border-bottom: 1px solid $accentColour;
            border-top: 1px solid $accentColour;
            padding-bottom: 2px;
            text-decoration: none;
            color: #999;
            @include fader(all, 1s, ease-in-out);
    
            &:hover{
                color: $accentColour;
                @include fader(all, 1s, ease-in-out);
            }
        }
    }
  }