$accentColour: rgb(149, 199, 245);

@mixin fader($what: all, $time: 0.4s, $how: ease-in-out) { 
    -webkit-transition: $what $time $how;
    -moz-transition: $what $time $how;
    transition: $what $time $how;
    
}

.nav {
    background-color: rgb(255, 255, 255);
    padding-bottom: 0px;
    margin: auto;
    box-shadow: 0px -5px inset $accentColour;


    ul {
        padding: 0;
        border: none;
        margin: 0;
        list-style: none;
        padding-bottom: 10px;
        padding-top: 10px;
        margin-bottom: 10px;
        
        
    }
    a {
        text-decoration: none;
        color: #999;
        border-right: 1px solid $accentColour;
        @include fader(all, 1s, ease-in-out);
        padding-right: 10px;

        &:visited {
            color: #999;
        }

        &:hover{
            @include fader(all, 1s, ease-in-out);
            color: $accentColour;
            
        }
    }

    li {
        display: inline-block;
        padding-left: 15px;
        
        
    }   
}
.contactButton { 
    float: right;
    padding-left: 0px;
    padding-right: 15px;
    
    a {
        border-right: none;
    }
}

.artButton {
    a {
        border-right: none;
    }
}
